import uniRequest from 'uni-request'
export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 0,
			flist: [],
			slist: [],
		}
	},
	onLoad(){
		this.loadData();
	},
	methods: {
		async loadData(){
			let response = await uniRequest.get('/mobile/get/goods/category/');
			console.log(response)
			if(response.status === 200){
				var slist = []
				this.flist = response.data
				this.flist.forEach(ele=>{
					this.slist.push(ele.subs)
				})
				console.log(this.slist)
				
				/* this.flist.forEach(ele=>{
					this.slist.push(ele.subs)
					console.log(this.slist)
					this.slist.forEach(el=>{
						this.tlist.push(el)
						console.log(this.tlist)
					})
				}) */
				
			}else{
				
			}
			/* let list = await this.$api.json('cateList');
			list.forEach(item=>{
				if(!item.pid){
					this.flist.push(item);  //pidΪ����id, û��pid����pid=0��һ������
				}else if(!item.picture){
					this.slist.push(item); //û��ͼ����2������
				}else{
					this.tlist.push(item); //3������
				}
			}) */
		},
		//һ��������
		tabtap(item){
			console.log(item)
			if(!this.sizeCalcState){
				this.calcSize();
			}
			this.currentId = item.id-1;
			let index = this.slist.findIndex(sitem=>sitem.subs === item.subs);
			console.log(index)
			// this.tabScrollTop = this.slist[index].top;
		},
		//�Ҳ�������
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].pid;
			}
		},
		//�����Ҳ���ÿ��tab�ĸ߶ȵ���Ϣ
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				console.log(item[0])
				let view = uni.createSelectorQuery().select("#main-" + item[0].id);
				console.log(view)
				view.fields({
					size: true
				}, data => {
					item[0].top = h;
					h += item[0].height;
					item[0].bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(tid){
			uni.navigateTo({
				url: `/pages/product/list?tid=${tid}`
			})
		}
	}
}
